import React, { useEffect } from 'react'
import './style.css'
import * as dultlogo from '../../assets/Clean Walk/Loading_Screen/LD_DULT_Logo.png'

import { useColourObjectStore } from '../../store/ColourStore'
import { logostore } from '../../store/logoStore';
import { logopositionstore } from '../../store/logopositionStore';
import { instancestore } from '../../store/InstanceStore';


const Splash = (props: any) => {

  const { loaddefaultcolor, loadinstancecolor } = useColourObjectStore();
  const { activeInstanceData } = instancestore();
  const { logo, loadlogowithid } = logostore();
  const { logoposition, loadlogo_arrayposition_iid } = logopositionstore();

  useEffect(() => {

    if (activeInstanceData !== false) {
      loadinstancecolor(activeInstanceData.id)
      if (!logo) { loadlogowithid(activeInstanceData.id) }
      if (!logoposition) { loadlogo_arrayposition_iid(activeInstanceData.id) }
    } else {
      loaddefaultcolor()
      if (!logo) { loadlogowithid(37) }
      if (!logoposition) { loadlogo_arrayposition_iid(37) }
    }

  }, [activeInstanceData])

  const logoimage_position = ((obj: any) => {
    const selectcase = (number: any) => {
      if (number === 1)
        return "logostyle1";
      else
        if (number > 1 && number < 4)
          return "logostyle2";
        else
          if (number >= 4 && number <= 6)
            return "logostyle3";
          else
            if (number > 6)
              return "logostyle4";
    }
    let a = selectcase(obj.length)
    return obj.map((obj1: any) => {
      return <img className={a} src={obj1.i_logo} />
    })
  })

  return (
    <div className="logos_section">

      <div className="row1">
        <div className="loadinnerrow">
          <img className="logostyle1" src={dultlogo} />
        </div>
      </div>

      {(logoposition?.section2?.length > 0) &&
        <div className='row2'>
          <div className="innerrowtitle"><u>PARTNERS</u></div>
          <div className="loadinnerrow">
            {(logoposition) && logoposition?.section2?.map(logoimage_position)}
          </div>
        </div>
      }

    </div>
  )
}

export default Splash
